import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Avatar from '@material-ui/core/Avatar';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import TuneIcon from '@material-ui/icons/Tune';
import { observer} from 'mobx-react';
import { Link } from "react-router-dom";
import theme from '../../theme'
import { Routes } from '../../Routes'


const useStyles = makeStyles(theme => ({
    root:{
        background: "linear-gradient(to top, transparent, rgb(0, 0, 0))",
        boxShadow: "none",
    },
    toolBar:{
        display: "flex",
        alignItems: "start",
        paddingLeft:theme.spacing(1.5),
        paddingRight    :theme.spacing(1.5),
        minHeight: 80,
    },
    divider: {
      height: 30,
      margin: theme.spacing(1),
    },
    button:{
        margin: theme.spacing(0.5),
    },
    logo:{
        position: "relative",
        padding: theme.spacing(0.5),
    },
    title:{
      paddingTop:10,
      paddingRight:4,
      color:"#fff"
    },
    subTitle:{
      color:"#fff",
      fontWeight: "400",
      lineHeight: "0.57"
    },
    formControl:{
      color:"#fff",
    },
    tuneIcon:{
      position: "absolute",
      right: theme.spacing(1.5),
      top: theme.spacing(1),
    }
}));


const useStylesSelect = makeStyles({
  root: {
    color: '#8bc34a',
    paddingTop: 11,
    paddingBottom: 11,
    paddingLeft: 10,
  },
  icon: {
    color: '#8bc34a',
  },
});


export default observer(({mangaStore,chapterId,mangaId}) => {
  const classes = useStyles();
  const classesSelect = useStylesSelect();
  // console.log(mangaStore)
  return (
        <AppBar className={classes.root}>
        <Toolbar className={classes.toolBar}>
          
       

        <Avatar component={Link} to={'/'}  src="/l.png" className={classes.logo}/>
        <Link to={Routes.Manga.link(mangaId)}>
          <Typography variant="button" component="h6"  className={classes.title}>
            {mangaStore.manga.isLoading||mangaStore.manga.title}
          </Typography>
          <Typography variant="subtitle2 button" component="h6"  className={classes.subTitle}>
            {mangaStore.manga.isLoading||mangaStore.manga.englishTitle}
          </Typography>
        </Link>
        <FormControl variant="outlined" >
          <Select
            classes={classesSelect}
          
            value={chapterId}
            // onChange={handleChange}
            // labelWidth={labelWidth}
            // input={<FilterInput />}
            // color="primary"
          >
            {
            mangaStore.manga.isLoading||mangaStore.manga.chapters&&mangaStore.manga.chapters.map(chap=>(
              <MenuItem component={Link} to={Routes.ReadManga.link(mangaStore.manga.id,chap.id)} key={`select_chap_${chap.id}`} value={chap.id}>{`${chap.volume}-${chap.chapter}`}</MenuItem>
            ))
            }
          </Select>
        </FormControl>
        {/* <IconButton size="small" color="primary" className={classes.tuneIcon}>
          <TuneIcon />
        </IconButton> */}
            </Toolbar>
        </AppBar>
  );
})

