import React, {useEffect,useState} from 'react';
import { observer, inject } from 'mobx-react';

import { makeStyles } from '@material-ui/core/styles';

import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import "./MangaReadPage.css"

import Grid from '@material-ui/core/Grid';
import Nav from '../Components/navbar/read'
import Button from '@material-ui/core/Button';
import { Link, useParams } from "react-router-dom";
import { Routes } from '../Routes'
import { LazyImage } from './../Components/lazyImg'
import { throttle } from 'lodash';
const useStylesContainer = makeStyles({
  root: {
    minHeight: "100vh"
  },
  select: {
    bottom: 10,
    left: 10,
    position: "fixed",
  }
});

const useStylesSelectPage = makeStyles({
  '@global': {
    body: {
      background: "rgb(33, 33, 33)"
    },
  },
  root: {
    color: '#8bc34a',
    paddingTop: 11,
    paddingBottom: 11,
    paddingLeft: 10,
    background: "rgb(33, 33, 33)"
  },
  icon: {
    color: '#8bc34a',
    transform: "rotate(180deg)"
  },
  iconOpen: {
    transform: "rotate(0deg)"
  }
});


const PageNavigator = observer(({mangaStore,chapterId,mangaId}) => {
  if(mangaStore.isLoading) return null
  const currentIndex = mangaStore.manga.chapters.findIndex(ch=>ch.id==chapterId)

  if(currentIndex!==mangaStore.manga.chapters.length-1){
    var before = mangaStore.manga.chapters[currentIndex+1].id
  }
  if(currentIndex>0){
    var next = mangaStore.manga.chapters[currentIndex-1].id
  }
  return <>
    {
    before&&<Button component={Link} to={Routes.ReadManga.link(mangaId, before)} style={{margin:12}}  size="large" color="secondary" >
    Предыдущая глава
    </Button>
    }
    {
    next&&<Button style={{margin:12}} component={Link} to={Routes.ReadManga.link(mangaId, next)} size="large" color="secondary" >
    Следующая глава
    </Button>
    }
    </>
})

const MangaReadPage = observer(({mangaStore, chapterStore}) => {
  let imgListRef = React.createRef();
  let { mangaId, chapterId } = useParams();

  useEffect(()=>{

    if(parseInt(mangaId)!==mangaStore.manga.id){
      mangaStore.fetchManga(mangaId)
    }
    chapterStore.fetchChapter(chapterId)
  },[chapterId])
  const classes = useStylesContainer();
  const classesSelect = useStylesSelectPage();
  return(

    <div style={{display:"flexColum", justifyContent:'center'}}>
      <Nav mangaStore={mangaStore} mangaId={mangaId} chapterId={chapterId}/>
      <ImgNavigator imgListRef={imgListRef} chapterStore={chapterStore} classesSelect={classesSelect} classes={classes}/>
      <Grid direction='column' ref={imgListRef} container spacing={2} justify='center'>
        {
            chapterStore.isLoading||chapterStore.chapter&&chapterStore.chapter.imgList.map((imgData,i)=>{
              return (
              <Grid style={{display:'flex'}} justify="center" xs={12} id={i+1} key={`chpater_read_img_${i}`} item >
                 <LazyImage style={{maxWidth:'100%', minHeight:300}} src={imgData.url} />
              </Grid>
              )
            })
        }
        <Grid  style={{display:'flex',width:'100%',justifyContent:'center'}} item >
          <PageNavigator mangaStore={mangaStore} chapterId={chapterId} mangaId={mangaId}/>
        </Grid>
      </Grid>

    </div>
)}
)

function ImgNavigator({imgListRef,chapterStore,classesSelect,classes}){

  let [value, setValue] = useState(1);
  const scroll = throttle(() => {
    if(!imgListRef.current){
      return
    }
    const h = window.innerHeight/2
    const id = [...imgListRef.current.children].find(el=>(el.getBoundingClientRect().top+h>0)).id
    setValue(id)
  }, 2000)
  useEffect(() => {
    window.addEventListener('scroll', scroll);
    return () => window.removeEventListener('scroll', scroll)
  }, [imgListRef])
return(
  <FormControl variant="outlined" >
  <Select
    classes={classesSelect}
    className={classes.select}
    value={value||(chapterStore.chapter.imgList?chapterStore.chapter.imgList.length:0)}
  >
      {
        chapterStore.isLoading||chapterStore.chapter&&chapterStore.chapter.imgList.map((_,i)=>(
          <MenuItem component='a' href={`#${chapterStore.chapter.imgList.length-i}`} value={chapterStore.chapter.imgList.length-i}>
            {`${chapterStore.chapter.imgList.length-i}/${chapterStore.chapter.imgList.length}`}
          </MenuItem>
        ))
      }
    </Select>
  </FormControl>
)
}

export default inject("mangaStore","chapterStore")(MangaReadPage);
