import { createMuiTheme } from '@material-ui/core/styles';

export default createMuiTheme({
    palette: {
      type: localStorage.getItem('dark')?'dark':'light',
      primary: {
        main: '#8bc34a'//#03a9f4
      },
      secondary: {
        main: '#8bc34a'
      },
    },
    overrides: {
      MuiPaper: {
        boxShadow: "0 3px 12px 0 rgba(218,224,235,.6)",
        elevation1: {
          boxShadow: "0 3px 12px 0 rgba(218,224,235,.6)"
        },
        elevation2: {
          boxShadow: "0 5px 26px 0 rgba(218,224,235,.6)",
        },
        elevation3: {
          boxShadow: "0 8px 20px 0 rgba(218,224,235,.6)",
        },
      }
    }
});